* {
  box-sizing: border-box;
}
li {
  list-style-type: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-info {
  border: 3px;
  border-color: black;
  width: 200px;
}
.card-img {
  width: 150px;
}

.img {
  height: 50px;
  width: 60px;
}

.list-item {
  display: flex;

  align-items: center;
}

.text {
  padding-left: 10px;
}

h2 {
  font-size: 30px;
}
